import React from 'react'
import {Link} from 'react-router-dom'

export default class MyFollowingCard extends React.Component {
    constructor(props){
        super(props)
    }

    render(){ 
        const info = this.props.cardInfo
        return( 
        <div class="card bg-transparent col-3 m-2  border-light text-light" >
            <div class="card-body d-flex justify-content-between ">
                <Link to={'/profil/'+info.username} ><h5 class="card-title text-light ">{info.username}</h5></Link>
                <button className='btn bg-transparent text-light border'><i className='fas fa-eye'></i></button>
            </div>
        </div>)
    }
}