import React from 'react'
import './search.css'
import Autocomplete from  './autoC'
import FormStateManager from '../../../helpers/formStateManager'
import GrisFond from './fond'

export default class Search extends React.Component {
    constructor(props){
        super(props)
        this.state={
            input:{
                search:'',   
            },
            Fond:false,
        }
    }

    inputChange = (e) =>{
        const res = FormStateManager.inputChange(e,new Array('search'))
        let input =  this.state.input
        input[res[0][0]] = res[1]
        this.setState({input})
    }

    fondEnabled = (Fond) =>{
        this.setState({Fond})
    }


    render(){
        return <div className=''>
        <form class="d-flex mx-3 search-box">
        <input class="form-control  bg-dark text-white rounded-0 "
        type="search" placeholder="Search" aria-label="Search" id='search' autoComplete='false' value={this.state.input.search} onClick={(e)=>{this.fondEnabled(true)}} onChange={(e)=>{this.inputChange(e)}}
        />
        <em class="btn bg-dark border text-light rounded-0" type='button'><i className='fas fa-search'></i></em>
        </form>
        <div className="d-flex mx-3">
        {/*<Autocomplete History={this.props.History} fondEnabled={this.fondEnabled} />*/}
        </div>
        <GrisFond fond={this.state.Fond} fondEnabled={this.fondEnabled} />
        </div>

            
    }
}
