import React  from 'react'
import NewProduits from './newProduits/newProduits'
import UpdateProduits from './updateProduits/update'
import UserManagement from './userManage/userManage'


export default class DashboardRouter extends React.Component{

    constructor(props){
        super(props)
    }

    render(){
        const route = this.props.History.match.params.app
        console.log(route)
        let RouteRender
        if (route =='produit') RouteRender = NewProduits
        else if (route =='updateProduits') RouteRender = UpdateProduits
        else if (route =='userManagement') RouteRender = UserManagement
        else return RouteRender = <></>

        return <RouteRender  
                    UserInfo={this.props.UserInfo} 
                    History={this.props.History} 
                    SetLoaderState={this.props.SetLoaderState} 
                    Theme={this.props.Theme} 
                />
    } 
}