import React from 'react'
import axios from 'axios'
import env from '../../../config/api'
import LoginView  from './loginView'
import LoginStore from './loginStore'
import flashed from '../../../helpers/flashed'
import { setSession } from '../../../helpers/session'


export default class Login extends React.Component {
  constructor(props){
    super(props)
    this.state=LoginStore
}

componentDidMount(){
  this.register_success() 
}

register_success = () =>{
  const data = flashed('get')
  // console.log(data);
  if (!data) return 0
  let input = this.state.input
  setTimeout(() => {
    input.username = data.username
    input.password = data.password
    this.error_ocurred('successful register ! now sign in')
  }, 1000);
  

}

loading = (start=true) => {
  let loading = this.state.loading
  const current =start
  loading.btn=current
  loading.spiner=current

  this.setState({loading})
}

form_control = (valide=true) =>{
  const is_valide=' '
  const is_invalide='is-invalid'
  this.setState({form_control:valide?is_valide:is_invalide})
}

alert_control = (show=true,alert_msg) =>{
  this.setState({alert:show})
  this.setState({alert_msg})
  // console.log(alert_msg);
}

error_ocurred = (msg) =>{
  this.setState({alert_msg:msg})
  this.form_control(false)
  this.alert_control(true,msg)
  setTimeout(() => {
    this.form_control(true)
    this.alert_control(false,msg)
  }, 2000);

}
success_login = (data={}) =>{
  setSession('Token',data.token)
  setSession('userInfo',{
    id:data.userId,
    username:data.username,
    admin:data.admin,
    token:data.token
  })
  this.props.history.push('/')
  this.refreshPage()
}

inputChange = (e) =>{
  // console.log(e.target.value);
  const id=e.nativeEvent.target.id
  const defined_id=[
    ['username','username226'],
    ['password','password226']
  ]
  const true_id=defined_id.map((index)=>{
    if (id==index[1]){
      return index[0]
    }
  }).filter((data)=>{
    if(data!=undefined){
      return data
    }
  })
  let input = this.state.input
  input[true_id[0]] = e.target.value

  this.setState({input})
}
submited=(e)=>{
  // console.log(e);
}

refreshPage(){ 
  window.location.reload(); 
}

login =(e) =>{
  this.loading(true)
  e.preventDefault()
  const url = env.hostname+env.routes.login+this.state.input.username+'/'+this.state.input.password
  axios.get(url)
    .then((res)=>{
      if(res.data.result==null){
        this.error_ocurred(res.data.error)
      }
      else if(res.data.result!=null){
        this.success_login(res.data.result)
      }
    }).catch((res)=>{
      this.error_ocurred('bad requests')
    }).finally(()=>{
      this.loading(false)
    })
}
  render(){
    return <LoginView
      viewController={this.state}
      inputChange={this.inputChange}
      submited={this.submited} 
      login={this.login} 
    />
  }
}
