import React from 'react'

export default class Alert extends React.Component {
  constructor(props){
    super(props)
    
  }
  componentDidUpdate(){
      // console.log(this.props);
  }

    render(){
        let msg = 'identifient incorrect ! veillez ressayez'
        if (this.props.msg!==undefined) msg = this.props.msg
        if(this.props.active)return(
            <div class="alert alert-dark" role="alert">
                {msg}
            </div>
        )
        else return(
            <></>
        )
    }
}
