import React from 'react'
import FormStateManager from '../../../helpers/formStateManager'

export default class Search extends React.Component {
    constructor(props){
        super(props)
        this.state={
            Fond:false,
        }
    }

    inputChange = (e) =>{
        const res = FormStateManager.inputChange(e,new Array('search'))
        this.props.updateSearch(res[1])
    }

    fondEnabled = (Fond) =>{
        this.setState({Fond})
    }


    render(){
        return <div className='my-3'>
            <form class="d-flex mx-3 search-box">
                <input class="form-control  bg-transparent text-center text-white rounded-0 "
                type="search" placeholder="rechercher un produit "
                aria-label="Search" id='search' autoComplete='false'
                value={this.props.Search} onChange={(e)=>{this.inputChange(e)}}
                />
            </form>
        </div>       
    }
}
