import React from 'react'
import './profil.css'
import Logout from '../../helpers/logout'
import { getSession } from '../../helpers/session'

export default class Gestion extends React.Component {
    constructor(props){
        super(props)
    } 

    isMyProfile = () =>{
        if(getSession('userInfo')==null) return false
        if(this.props.History.match.params.username==getSession('userInfo').username ) return true
        else return false
    }

    refreshPage(){ 
        window.location.reload(); 
    }

    render(){
        if(this.isMyProfile())return <>
                <div className="col-2  d-flex justify-content-center py-5">
                    <div className="row">
                        <button className='col-10 m-2 btn bg-transparent border  text-light border-light'  onClick={(e)=>{this.props.History.history.push('/new/upload')}} > new </button>
                        <button className='col-10 m-2 btn bg-transparent border  text-light border-light' onClick={(e)=>{Logout(this.props.History.history);this.refreshPage()}}> log out </button>
                    </div>
                    
                </div>
        </>
        else return <></>
    }
}