import React from 'react'
import api from '../../../config/api'
import axios from 'axios'
import './card.css'
import {textShurt,numShurt} from '../../../helpers/shurtner'
import Modal from '../../home/controlui/modal'
import UpdateProduitInfo from './updateProduitInfo'
import { getSession } from '../../../helpers/session'


export default class produitdCard extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      user:null,
      isReady:false,
      produitInfo:{}
    }
}

  componentDidMount(){
  }

  openProduit = (id) =>{
    this.props.History.history.push('/produit/'+id)
  }


  deleteProduits = () =>{
    this.props.deleteProduits(this.props.produit.id)
  }

  UpdateProduitInfo = (produitInfo) =>{
    this.setState({produitInfo})
    console.log(produitInfo)
  }

  setProduitInfo = () =>{
    let data ={}
    data.produitInfo = this.state.produitInfo
    data.Token = getSession('Token')
    data.userInfo = getSession('userInfo')
    axios.post(api.hostname+api.routes.sudoUpdateProduitsInfo,data).then((res)=>{
      console.log(res.data)
      if(res.data.result!=null){
        this.props.getAllPrdouitsInfo()
      }
    }).catch((err)=>{

    }).finally(()=>{

    })
  }

    render(){
        return(
            <div class=" col-10 btn box rounded-0">
            <div class="card bg-transparent text-white border-0 rounded-0 my-1 " >
            <div class="card-body d-flex justify-content-around">
                <p className='align-self-center'>{this.props.index}</p>
                <h5 class="card-title text-primary h5  align-self-center" title={this.props.produit.nom}>{textShurt(this.props.produit.nom,20)}</h5> 
                <p class="card-text align-self-center" title={this.props.produit.description}>{textShurt(this.props.produit.description,30)} </p> 
                <p className='align-self-center'>{this.props.produit.stock}</p>
                <div className="row ">
                  <Modal History={this.props.History} 
                    message = {{ msg:<UpdateProduitInfo UpdateProduitInfo={this.UpdateProduitInfo}  History={this.props.History} Produit={this.props.produit} />,confirm:'valider',refuse:'annuler'}}
                    confirm={(e)=>{this.setProduitInfo()}}
                    refuse={(e)=>{}}
                    ModalButton={{class:'btn col-4 align-self-center text-light rounded-0 btn-icon  top-100',msg:<i className='fas fa-plus'></i>,box_class:'col-lg-4 col-6 '}}
                  />
                  <Modal History={this.props.History} 
                  message = {{ msg:<p className=''>vous allez suprimer <strong>{this.props.produit.nom}</strong> du stock des produits <br />attention cette opreation est irreversible <br /> <strong className="h6"> voulez vous continuez ? </strong>  </p>,confirm:'oui',refuse:'non'}}
                  confirm={(e)=>{this.deleteProduits()}}
                  refuse={(e)=>{}}
                  ModalButton={{class:'btn col-4 align-self-center text-light rounded-0 btn-icon ',msg:<i className='fas fa-trash '></i>,box_class:' col-3 '}}
                />
                  <em className='btn col-4 align-self-center text-light rounded-0 btn-icon '><i className='fas fa-eye ' onClick={(e)=>{this.openProduit(this.props.produit.id)}}></i></em>
                </div>
                
            </div>
        </div>
        </div>
        )
    }
}
