import React from 'react'
import Watcher from '../../home/controlui/wacther'
import {getSession,setSession} from '../../../helpers/session'
import axios from 'axios'
import api from '../../../config/api'
import ProduitCard from './produitcard'
import ProduitTotal from './total'
import CardTitle from './cardTitle'
import redirect from '../../../helpers/redirect'
import Modal from '../../home/controlui/modal'

export default class Panier extends React.Component {
    constructor(props){
      super(props)
      this.SetLoaderState = this.props.SetLoaderState
      this.state={
        panier:[],
        watch:0
      }
      
    }

    componentDidMount(){
      this.loadPanier()
    }

    componentDidUpdate(){
      this.watchPanier()
    }

    Tracked = () =>{
      console.log('tacked')
    }

    watchPanier = () =>{
      console.log(this.state.watch,this.props.panierIsUpdated)
      if(this.state.watch!=this.props.panierIsUpdated) {
        this.setState({watch:this.props.panierIsUpdated})
        this.loadPanier()
      }
    }

    loadPanier = () => {
      const panier = getSession('panier')
      this.setState({panier})
    }

    totalCount = () =>{
      let quantite=0
      let price=0
      this.state.panier.map((produit)=>{
        quantite+=produit.saved.quantite
        price+=(produit.prix_de_vente*produit.saved.quantite)
      })
      return {quantite,price}
    }

    removeFromPanier = (id) =>{
      let panier = getSession('panier')
      panier = panier.filter((produit)=>{
        if(produit.id!=id) return produit
      })
      setSession('panier',panier)
      console.log(id);
      this.loadPanier()
    }

    sell = (id,stock,produit) =>{
      this.SetLoaderState(true)
      const produitTable = produit
      const data = {
        produitInfo:{id,stock,produitTable},
        userInfo:getSession('userInfo'),
        Token:getSession('Token'),
      }
      axios.post(api.hostname+api.routes.SellProduits,data).then((res)=>{
        if(res.data.result!=null){
          // this.getProduitInformation()
          this.removeFromPanier(id)
          console.log('selled')
          redirect(this.props.History,this.History.match.url)
        }
      }).catch((err)=>{

      }).finally(()=>{
        this.SetLoaderState(false)
        this.props.panierIsSell()
      })
    }

    sellPanier = () =>{
      const panier = getSession('panier')
      panier.map((produit)=>{
        this.sell(produit.id,produit.saved.quantite,produit)
      })
      setSession('panier',panier)
      this.loadPanier()
    }

    clearPanier = () =>{
      setSession('panier',[])
      this.loadPanier()
    }

    ModalJsx = () =>{
      return <div className='col-12 row text-center'>
          <p className="col-12 text-center"> 
            vous allez vendre <strong> {this.state.panier.map((produit)=>{
              return <> {produit.saved.quantite} unite de  {produit.nom } , </>
            })} </strong> le touts a 
            <strong> {this.totalCount().price} FCFA </strong> 
          </p> 
          <br /> 
          <p className='col-12 h6 text-center'> 
            voulez vous continuez ? 
          </p>
        </div>
    }

    emptyPanierJsx =() =>{
      return <div>
      <Watcher History={this.props.History} Tracked={this.Tracked} />
      <div class="">
        <div className="row ">
          <p className='text-white h5 m-4 px-3 underline'>Gestionnaire De Panier </p> 
          <hr/>
        </div>
        <p className='text-light h3 border rounded-0 p-3 text-center'>le panier est vide</p>
      </div>
    </div>
    }
  

    render(){
      const panier =  this.state.panier
      const emArray = [0]
      if (panier==null ) return this.emptyPanierJsx()
      else if(panier.length<1) return this.emptyPanierJsx() 
      else return(
      <div>
        <Watcher History={this.props.History} Tracked={this.Tracked} />
        <div class="">
            <div className="row ">
              <p className='text-white h5 m-4 px-3 underline'>Gestionnaire De Panier </p> 
              <hr/>
              <div class=" col-12 ">
                <div class="card bg-transparent text-white border rounded-0 my-2  " >
                <div class="card-header ">
                  <div className="row p-2">
                      <p className='h5'>panier 236</p>
                    </div>
                  </div>
                  <div class="card-body ">
                  <CardTitle />
                    {
                      panier.map((produit)=>{
                        return <ProduitCard produit = {produit} removeFromPanier={this.removeFromPanier} />
                      })
                    }
                    {emArray.map((data)=>{
                      const count =this.totalCount()
                      if (this.state.panier.length>=1) return <ProduitTotal count = {count} />
                    })}
                    <div className="row p-2">
                    <Modal History={this.props.History} 
                      message = {{ msg:this.ModalJsx(),confirm:'oui',refuse:'non'}}
                      confirm={(e)=>{this.sellPanier()}}
                      refuse={(e)=>{}}
                      ModalButton={{class:'btn p-2  btn-transparent text-light border rounded-0 col-3 mx-3 ',msg:'Vendre ',box_class:' col-3 '}}
                    />
                    <Modal History={this.props.History} 
                      message = {{ msg:<p className='h6'> voulez vous remettre le panier a zero ? <br /> attention cette operation est irreversible </p>,confirm:'oui',refuse:'non'}}
                      confirm={(e)=>{this.clearPanier()}}
                      refuse={(e)=>{}}
                      ModalButton={{class:'btn p-2  btn-transparent text-light border rounded-0 col-3 mx-3 ',msg:'reinitialiser ',box_class:' col-3 '}}
                    />
                    </div>
                  </div>
                </div>
              </div>
            </div>
        </div>
      </div>
      )
      
    }
}
