import React from 'react'
import {Link} from 'react-router-dom'

export default class ProduitCard extends React.Component {
    constructor(props){
        super(props)
    }


    render(){
        const produit = this.props.produit
        return <div className="row border-bottom border-light  rounded-0 py-3 d-flex px-2 ">
        <Link className='col-4 align-self-center h6 text-decoration-none text-primary ' to={'/produit/'+produit.id}>{produit.nom}</Link> 
        <div className="col-3 align-self-center">
          <input class="form-control  bg-dark text-white rounded-0 text-center" type="text" placeholder="nombre"
          aria-label="Search" id='num' autoComplete='false' value={produit.saved.quantite} disabled='true' />
        </div>
        <div className="col-4  h6">
          <p className='align-self-center'>{produit.saved.quantite*produit.prix_de_vente} FCFA</p>
        </div>
        <button className='btn  btn-transparent text-light border rounded-0 col-1 px-3 ' onClick={(e)=>{this.props.removeFromPanier(produit.id)}} title={' suprimer du panier'}>  <i className='fas fa-trash text-light'></i></button>
      </div>   
    }
}
