import React from 'react'
import FST from '../../../helpers/formStateManager'
import {getSession,setSession} from '../../../helpers/session'
import Watcher from '../../home/controlui/watcher'
import Modal from '../../home/controlui/modal'

export default class ProduitsCard extends React.Component {
  constructor(props){
    super(props)
    this.cssClass=this.props.CssClass
    this.theme=this.props.Theme
    console.log(this.props.Theme)
    this.state={
      panier:{
        number:0
      },
      input:{
        num:0
      }
    }
  }

  panier_prix = () =>{
    return this.props.Informations.prix_de_vente*this.state.panier.number
  }

  inputAcceptable = (value) =>{
    console.log(value,this.props.stock)
    if(value>this.props.Informations.stock){
      let panier  = this.state.panier
      panier.number=this.props.Informations.stock
      this.setState({panier})
      return false
    } 
    else if (value<0) return false
    return true
  } 

  inputIsValide = (e) =>{
    const enter = e.target.value
    console.log(parseInt(enter))
    return parseInt(enter)
  }

  inputChange = (e) =>{
    const val = parseInt(this.inputIsValide(e))
    if(val=='NaN'){
      return 0
    }
    else if (typeof(val)==NaN) return 0
    const idMap = ['num']
    const res = FST.inputChange(e,idMap)
    let input =  this.state.input
    let panier = this.state.panier
    if(res[1].length<1) res[1]='0'
    panier.number = parseInt(res[1])
    // input[res[0][0]] = res[1]
    if(!this.inputAcceptable(panier.number)) return 0
    this.setState({input,panier})
    
     console.log(res,val);
  }

  panierSave = (data) =>{
    let panier = getSession('panier')
    let information  = this.props.Informations
    information.saved={
      quantite:this.state.panier.number,
      created : Date.now()
    }
    if(information.saved.quantite<=0) return 0
    
    if(panier==null){
      setSession('panier',[information])
      console.log('1 step');
    }
    else{
      panier=panier.filter((data)=>{
        if(data.id!=information.id) return data
      })
      
      panier.push(information)
      setSession('panier',panier)
      console.log('2 step');
    }
    this.setState({panier:{number:0}})
    console.log(panier,'saved')
  }

  ajouterPanier = (e) => {
    this.panierSave(this.props.Informations)
    this.props.panierIsUpdated()
  }

  routesTracker = () => {
    this.setState({panier:{number:0}})
  }

  clear = (e) => {
    this.setState({panier:{number:0}})
  }

  sell = (e) =>{
    this.props.sell(this.state.panier.number)
    this.setState({panier:{number:0}})
  }

  ModalJsx = () =>{
    return <div className='col-12 row text-center'>
        <p className="col-12 text-center"> 
          vous allez vendre <strong>{this.state.panier.number}</strong>  unite de 
          <strong> {this.props.Informations.nom} </strong>  a 
          <strong> {this.panier_prix()} FCFA  </strong> 
        </p> 
        <br /> 
        <p className='col-12 h6 text-center'> 
          voulez vous continuez ? 
        </p>
      </div>
  }
 

    render(){
return(
  <div>
    <Watcher History={this.props.History.match.params.id} Tracked={this.routesTracker} WatcherId={120}/>
  <div className='card-header text-light  bg-dark'> 
  </div>
    <div class="card-body  text-light bold">
      <div class="row">
        <div class="col-md-12">
          <div class="d-flex align-items-center pb-2">
            <div class="dot-indicator bg-secondary px-2 py-1 text-center text-light rounded-circle mx-2">ele</div>
            <p class={'mb-0 h4 text-center text-white'}> {this.props.Informations.nom} <em className='h6 text-secondary'> ~ {this.props.Informations.categories}</em></p>
          </div>
          <div className="col-12 h6 p-2">
            <p>Stock  : {this.props.Informations.stock}</p>
            
              
          </div>
          <div className=" text-light col-12 h6 border rounded-0 p-3 text-center ">
              {this.props.Informations.description}
          </div>
          <div className="container-fluid d-flex justify-content-center p-2 row">
            
            <div className="col-12  border rounded-0 p-2 my-2 ">
              <h4 className='px-2 text-center'>Calcule du prix </h4>
              <div className=' row py-3 d-flex justify-content-center'>
                <label htmlFor="num" className='col-2 align-self-center h6'> Quantiter</label>
                <div className="col-7 ">
                <input class="form-control  bg-dark text-white rounded-0 " type="text" placeholder="nombre"
                 aria-label="Search" id='num' autoComplete='false' value={this.state.panier.number} onChange={(e)=>{this.inputChange(e)}}
                />
                </div>
              </div>
              <div className="row px-5 py-2 d-flex justify-content-center">
              <p className='px-4' >
                <label className='px-2 h6' >Prix :</label>
                <strong className='h6 px-2'> {this.panier_prix()}</strong>  FCFA
              </p>
              <p className='px-4 h6' >
                <label className='px-2 ' >Stock restant apres vente :</label>
                <strong className=' px-2'> {this.props.Informations.stock-this.state.panier.number}</strong> 
              </p>
              </div>
              <div className="row p-3">
              <Modal History={this.props.History} 
                message = {{ msg:this.ModalJsx(),confirm:'oui',refuse:'non'}}
                confirm={(e)=>{this.sell(e)}}
                refuse={(e)=>{}}
                ModalButton={{class:'btn col-3 btn-transparent mx-1 text-light border rounded-0',msg:'Vendre ',box_class:' col-3 '}}
              />
              <button className='btn col-5 btn-transparent mx-1 text-light border rounded-0'  onClick={(e)=>{this.ajouterPanier(e)}}> ajouter au panier </button>
              <button className='btn col-3 btn-transparent mx-1 text-light border rounded-0' onClick={(e)=>{this.clear(e)}}> reinitialiser </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
</div>
      )
    }
}
