import React from 'react'
import api from '../../../config/api'
import axios from 'axios'
import ProduitsCard from './card'
import Watcher from '../../home/controlui/wacther'
import Loading from '../../home/mainPage/loading'
import Search from '../../home/mainPage/search'
import CardTitle from '../../home/mainPage/produitdCardTitle'
import { getSession } from '../../../helpers/session'

export default class UpdateProduits extends React.Component {
    constructor(props){
      super(props)
      this.state={
        Produits:[],
        ProduitsSaved:[],
        Search:'',
        trie:{
          index:'up',
          name:null,
          description:null,
          stock:null,
          active:'index'
        }
      }
    }
    componentDidMount(){
      this.getAllPrdouitsInfo()
    }

    Tracked = () => {
      this.getAllPrdouitsInfo()
    }

    getSourceName = () => {
      const title =this.props.History.match.params.title
      return title
    }

    updateSearch = (Search) =>{
      this.setState({Search})
      this.updateProduitsView(Search)
      console.log(Search,this.state.Produits,this.state.Search)
    }

    updateProduitsView = (search) =>{
      let Produits = this.state.ProduitsSaved
      const text = ''
      Produits = Produits.filter((produit)=>{
        console.log(produit.nom,search,produit.nom.search(search))
        if(produit.nom.search(search)!=-1 ){
          return produit
        }
      })
      this.setState({Produits})
    }
    
    getAllPrdouitsInfo = () => {
      this.props.SetLoaderState(true)
      const ProduitsLenght=10
      axios.get(api.hostname+api.routes.AllProduitsInfo ).then((res)=>{
        const Produits=res.data.result
        if(res.data.result!=null){
          console.log(Produits);
          this.setState({Produits,ProduitsSaved:Produits})
        }
        
        this.props.SetLoaderState(false)
      }).catch(()=>{

      })
    }

    trieFunction = (data) =>{
      let method
      const trie = this.state.trie[data]
      this.setState({trie:{
        index:null,
        name:null,
        description:null,
        stock:null,
      }})
      method = trie=='up'?'down':'up'
      
      const state_trie=this.state.trie
      state_trie[data]=method
      state_trie.active = data
      this.setState({trie:state_trie})
        
      this.trieEngine(data,method)
      
    } 

    trieEngine = (data,method) =>{
      let produits = this.state.Produits
      let trieTab = produits.map((produit)=>{
        return produit[data]
      })
      let trieTab2  = trieTab.sort()
      trieTab2 = trieTab2.reverse()
      
      let final_produits = produits.filter((produit)=>{
        let ret = undefined
        trieTab2.forEach((element,index) => {
          if(element == produit[data]){
            ret = element
            trieTab[index]=produit
          } 
        });
        if(ret!=undefined) return produit
      })
      
      this.setState({Produits:trieTab})
      console.log(trieTab)
    }

    deleteProduits = (id) =>{
      const data = {
        userInfo:getSession('userInfo'),
        Token:getSession('Token'),
        produitInfo:{id}
      }
      axios.post(api.hostname+api.routes.deleteProduit,data).then((res)=>{
        const result = res.data.result
        if(result!=null){
          this.getAllPrdouitsInfo()
        }
      }).catch((err)=>{

      }).finally(()=>{

      })
    }

    render(){
      const Produits = this.state.Produits
      if(this.state.ProduitsSaved.length<1) return <Loading />
      return(
      <div>
        <Watcher History={this.props.History} Tracked={this.Tracked} />
        <div class="">
            <div className="row d-flex justify-content-center ">
              <div className="row col-10">
              <p className='text-white h5 my-5 mx-3  underline'> la liste des produits </p>
                <div className="col-4">
                  <Search  History={this.props.History} 
                  updateSearch={this.updateSearch} 
                  Search={this.state.Search} />
                </div>
              </div>
              
              <CardTitle trie={this.state.trie} trieFunction = {this.trieFunction} />
              {Produits.map((produit,index)=>{
                produit.index=index
                return <ProduitsCard
                produit={produit} 
                index={index+1}
                Theme={this.props.theme} 
                History={this.props.History} 
                deleteProduits ={this.deleteProduits}
                getAllPrdouitsInfo = {this.getAllPrdouitsInfo}
                />})}    
            </div>
        </div>
      </div>
      )
    }
}
