import React from 'react'
import Modal from '../../../home/controlui/modal'

export default class UserInfo extends React.Component {
  constructor(props){
    super(props)
    this.state={
        userInfo:null
    }
  }

  componentDidMount(){
      this.userSellingJsonner()
  }

  titleJsx = () =>{
    if(this.state.userInfo.sellRegistre.length>=1)
      return <div className='row px-2'>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">date </p>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">unite vendu </p>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">prix de vente (FCFA)</p>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">marge de benefice  (FCFA)</p>
      </div>
    else return <div className='row px-2'>
      <p className='h5 m-3 text-center'> aucune vente enregister par cet utilisateur </p>
    </div>
  }

  dateBeautify =(date) =>{

    return ` ${date.Date}/${date.Month}/${date.Year}  ${date.Hours}:${date.Minutes}:${date.Secondes}`
  }

  userInfoJsx = () => {
    const userInfo = this.state.userInfo 
    if(userInfo!=null) return <div className='text-light'>
      <p className='h5 p-2'> derniere vente effectuer par <em>{userInfo.username}</em>  </p>
        <this.titleJsx />
      {userInfo.sellRegistre.map((registre)=>{
          return <div className='row text-center   m-2 px-4'>
              <p className="col-3 border-0 rounded-0 "> {this.dateBeautify(registre.date)}</p>
              <p className="col-3 border-0 rounded-0 ">{registre.produitInfo.stock}</p>
              <p className="col-3 border-0 rounded-0 ">{registre.produitInfo.produitTable.prix_de_vente*registre.produitInfo.stock} </p>
              <p className="col-3 border-0 rounded-0 "> {(registre.produitInfo.produitTable.prix_de_vente-registre.produitInfo.produitTable.prix_d_achats)*registre.produitInfo.stock} </p>
          </div>
      })}
    </div>
    else return <></>
  }

  userSellingJsonner = () => {
    let userInfo = this.props.User
    console.log(userInfo.sellRegistre)
    // userInfo.sellRegistre = JSON.parse(userInfo.sellRegistre)
    // userInfo.sellRegistre = []
    this.setState({userInfo})
    
  }

    render(){
      const user = this.props.User
        return <Modal History={this.props.History} 
            message = {{ msg:<this.userInfoJsx />}}
            modalCardStyle={{top:'20%',left:'17%'}}
            ModalButton={{class:'btn align-self-center text-light border-0 rounded-0 btn-icon mb-3',msg:<i className='fas fa-calendar '></i>,box_class:'col-8'}}
        />
    }
}
