import React from 'react'
import NavBar from '../home/navbar/navbar'
import Footer from '../section/footer'
import Loader from '../home/controlui/loader'
import theme from '../../data/state/theme'
import Watcher from '../home/controlui/watcher'
import isConnected from '../../helpers/isConnected'
import Route from './route' 
import DashboardMenu from './dashMenu/dashMenu'

export default class Dashboard extends React.Component {
    constructor(props){
        console.log(props);
        super(props)
        this.state={
            LoaderState:false,
            theme,
            themeActivate:'dark',
            userinfo:null
        }
        this.theme=this.state.theme[this.state.themeActivate]
    }

    componentDidMount(){
        if(isConnected()==false) {
            this.props.history.push('/login')
        }
    }
    

    SetLoaderState =(NewState=true) =>{
        this.setState({LoaderState:NewState})
    }
    routesTracker = () =>{
        console.log('routes change')
    }

    render(){
        return(
    <div className={this.theme.primary} >
        
        <Watcher History={this.props.match.params.step} Tracked={this.routesTracker} WatcherId={120}/>
        <div class="container-fluid page-body-wrapper container">
        <NavBar Theme={this.theme} History={this.props}/>
        <div className="row">
            <div className="col-4">
                <DashboardMenu History={this.props} />
            </div>
            <div className="col-8">
                <Route UserInfo={this.state.userinfo} History={this.props} SetLoaderState={this.SetLoaderState} Theme={this.theme} />
            </div>
        </div>
        
            <Footer Theme={this.theme} />
        </div>
    </div>
        )
    }
}