import React from 'react'
import {Link} from 'react-router-dom'
import api from '../../../config/api'
import axios from 'axios'
import './card.css'
import {textShurt,numShurt} from '../../../helpers/shurtner'

export default class produitdCard extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      user:null,
      isReady:false
    }
}

  componentDidMount(){
    setTimeout(() => {
      this.getAutorInfo() 
    }, 1000); 
  }

  openProduit = (id) =>{
    this.props.History.history.push('/produit/'+id)
  }

  getAutorInfo = () =>{
    const id = this.props.produit.user
    const url = api.hostname+api.routes.getMyRelation+id
    console.log(url)
    axios.get(url).then((res)=>{
      this.setState({user:res.data.result.username})
      this.setState({isReady:true})
      
    }).catch((err)=>{

    }).finally(()=>{
    })
  }

    render(){
        return(
            <div class=" col-10 btn box rounded-0" onClick={(e)=>{this.openProduit(this.props.produit.id)}}>
            <div class="card bg-transparent text-white border-0 rounded-0 my-1 " >
            <div class="card-body d-flex justify-content-around">
                <p className='align-self-center'>{this.props.index}</p>
                <h5 class="card-title text-primary h5  align-self-center" title={this.props.produit.nom}>{textShurt(this.props.produit.nom,20)}</h5> 
                <p class="card-text align-self-center" title={this.props.produit.description}>{textShurt(this.props.produit.description,30)} </p> 
                <p className='align-self-center'>{this.props.produit.stock}</p>
                <p className='btn align-self-center text-light border rounded-0 btn-icon '><i className='fas fa-eye '></i></p>
            </div>
        </div>
        </div>
        )
    }
}
