import React from 'react'
import {Link} from 'react-router-dom'

export default class MyPostCard extends React.Component {
    constructor(props){
        super(props)
    }

    render(){ 
        const post = this.props.cardInfo
        // console.log('render 122')
        return( 
        <div class="card bg-transparent col-3 mx-2 d-flex flex-column justify-content-between border-light text-light" >
            <div class="card-body ">
                <Link to={'/audio/'+post.src} ><h5 class="card-title text-light ">{post.title}</h5></Link>
                <h6 class="card-subtitle mb-2 text-muted">{post.artiste}</h6>
                <p class="card-text">{post.description}</p>
                <div className="d-flex justify-content-around">
                <em class=" text-center"><i className="fa fa-heart text-primary"></i><strong className="m-2 text-secondary">{post.like}</strong></em>
                <em class=" text-center"><i className="fa fa-eye  text-secondary"></i><strong className="m-2 text-secondary">{post.view}</strong></em>
                <a href='#' class="text-center"><i className="fa fa-bookmark  text-secondary"></i></a>
                </div>
            </div>
        </div>)
    }
}