import React from 'react'
import { getSession } from '../helpers/session'

export default class RouteWatch extends React.Component {
    constructor(props){
        super(props)
        this.state={
            history:null
        }
        // console.log(this.props.History.match.params)
    }

    componentDidUpdate(){
        this.updateHistory()
    }

    componentDidMount(){

    }

    appendHistory = (value) =>{
        let history =this.state.history
        this.setState({history:value})
      }
  
    routesIsUpdated = (value) =>{
        let history =this.state.history
        if(history===value) return false
        return true
    }
    
    updateHistory =() => {
        const History = getSession('history')
        const params = History.match.url
        if ( this.routesIsUpdated(params)===false ) return 0
        console.log(`watcher ${this.props.WatcherId} is updated`);
        this.appendHistory(params)
        this.props.Tracked()
    }

    render(){
        return<></>
    }
}