import React from 'react'
import Produits from '../Controler/produits'
import Panier from '../panier/panier'
import axios from 'axios'
import env from '../../../config/api'

export default class Body extends React.Component {
  constructor(props){
    super(props)
    this.state = {
        connected:null,
        panierIsUpdated:0,
        panierIsSell:0,
    }
  }

    componentDidMount(){
        this.testconnexion()
    }
    testconnexion = () =>{
    axios.get(`${env.hostname}${env.routes.connection} `).then((res)=>{
       this.setState({connected:true})
        
    }).catch((err)=>{
        this.setState({connected:null})
    })}

    panierIsUpdated = () =>{
      let panierIsUpdated = this.state.panierIsUpdated
      panierIsUpdated++
      this.setState({panierIsUpdated})
    }

    panierIsSell = () =>{
      let panierIsSell = this.state.panierIsSell
      panierIsSell++
      this.setState({panierIsSell})
      console.log('panier are selled')
    }

    render(){
        const connected = this.state.connected
    if(connected==null) return <div>loading ... </div>
    else if (this.state.connected==true) return(
      <div class=" row my-5 ">
        <div className="col-12  col-lg-6">
          <Produits 
            SetLoaderState={this.props.SetLoaderState}
            Theme={this.props.Theme} 
            History={this.props.History} 
            panierIsUpdated ={this.panierIsUpdated}
            panierIsSell = {this.state.panierIsSell }
          />
        </div>
        <div className="col-12  col-lg-6">
          <Panier 
            SetLoaderState={this.props.SetLoaderState}
            History={this.props.History}  
            Theme={this.props.Theme}
            panierIsUpdated={this.state.panierIsUpdated}
            panierIsSell= {this.panierIsSell}
          />
        </div>
      </div>
      )
    else if (this.state.connected==false) return <div>not connection</div>
    }
}
