import React from 'react'
import FST from '../../../helpers/formStateManager'
import api from '../../../config/api'
import axios from 'axios'
import {getSession} from '../../../helpers/session'

export default class Form extends React.Component {
    constructor(props){
        super(props)
        this.state={
            input:{
                nom:'',
                description:'',
                categories:'',
                prix_d_achat:'',
                prix_de_vente:'',
                stock:'',
                min_stock:''
            }
        }
    } 
    componentDidMount(){
        this.props.SetLoaderState()
        
    }

    goHome = () =>{
        this.props.History.history.push('/')
    }

    inputChange = (e) =>{
        const idMap = ['nom','categories','description','stock','prix_d_achat','prix_de_vente','min_stock']
        const res = FST.inputChange(e,idMap)
        let input =  this.state.input
        input[res[0][0]] = res[1]
        this.setState({input})
        // console.log(res);
    }

    submit = (e) =>{
        const input = this.state.input
        e.preventDefault()
        const data = {
            userInfo:getSession('userInfo'),
            Token:getSession('Token'),
            produitInfo:input
        }
        axios.post(api.hostname+api.routes.newProduits,data)
        .then((res)=>{
            if(res.data.result!=null){
                this.goHome()
            }
        })
        .catch((err)=>{

        })
    }
	
    render(){
        return( 
<div className="d-flex justify-content-center row  bg-transparent text-light">
    <form class="col-10 row border p-2 py-5 my-5 mx-2">
    <p className='text-center h2 pb-3 mb-3'> Enregistrer un nouveau produit</p>
    <div class="col-4 p-2 m-1">
      <input onChange={(e)=>{this.inputChange(e)}} value={this.state.input.title} type="text" class="form-control bg-transparent border rounded-0 text-light text-center" id="nom" placeholder={'nom du produit'}  required/>
    </div>
    <div class="col-4 p-2 m-1">
      <input  onChange={(e)=>{this.inputChange(e)}} value={this.state.input.categories} placeholder={'categories'} type="text" class="form-control bg-transparent border text-center rounded-0 text-light" id="categories" required/>
    </div>
    <div class="col-3 p-2 m-1">
      <input  onChange={(e)=>{this.inputChange(e)}} value={this.state.input.stock} placeholder={'stock'} type="text" class="form-control bg-transparent border text-center rounded-0 text-light" id="stock" required/>
    </div>
    <div class="col-4 p-2 m-1">
      <input onChange={(e)=>{this.inputChange(e)}} value={this.state.input.prix_d_achat} type="text" class="form-control bg-transparent border rounded-0 text-light text-center" id="prix_d_achat" placeholder={'prix d\'achat'}  required/>
    </div>
    <div class="col-4 p-2 m-1">
      <input  onChange={(e)=>{this.inputChange(e)}} value={this.state.input.prix_de_vente} placeholder={'prix de vente'} type="text" class="form-control bg-transparent border text-center rounded-0 text-light" id="prix_de_vente" required/>
    </div>
    <div class="col-3 p-2 m-1">
      <input  onChange={(e)=>{this.inputChange(e)}} value={this.state.input.min_stock} placeholder={'alert quand le stock atteint'} type="text" class="form-control bg-transparent border text-center rounded-0 text-light" id="min_stock" required/>
    </div>
    <div class="col-12 p-2 m-1">
        <textarea onChange={(e)=>{this.inputChange(e)}} maxLength={1000} value={this.state.input.description} class="form-control  bg-transparent border rounded-0 text-center text-light" id="description" placeholder="enter la description du produit ici" required />
    </div>
    <button onClick={(e)=>{this.submit(e)}} className='col-3 m-2 btn bg-transparent border  rounded-0 text-light p-3' > valider </button>
    </form>
</div>
)
    }
}