import React from 'react'


export default class Search extends React.Component {
    constructor(props){
        super(props)
    }

    render(){
        let style = {
            backgroundColor: 'rgba(0, 0, 0, 0.404)',
            height: '100000px',
            width: '1000%',
            alignSelf: 'center',
            position: 'fixed',
            zIndex: '100',
            right: '3px',
            top: '0px',
        }
        if(this.props.Zindex){
            style.zIndex = this.props.Zindex
        }
        if(this.props.fond) return <div style={style}  className='gris-fond'  onClick={(e)=>{this.props.fondEnabled(false)}}>
            <p></p>
        </div>
        else return <></>        
    }
}
