import React from 'react'
import MyPost from './tabs/myPost'
import MySaved from './tabs/mySaved'
import MyFollowing from './tabs/myFollowing'
import MyFollowers from './tabs/myFollowers'
import MyHistory from './tabs/myHistory'

export default class Tabs extends React.Component {
    constructor(props){
        super(props)
    }

    render(){
        const activeTabs = this.props.ActiveTabs 
        if(activeTabs===0){
            return <MyPost   
                        TabsInfo={this.props.TabsInfo}
                        UserInfo={this.props.UserInfo}
                        History={this.props.History}
                        SetLoaderState={this.props.SetLoaderState} 
                    />
        }         
        else if(activeTabs===1){
            return <MySaved 
                        TabsInfo={this.props.TabsInfo}
                        UserInfo={this.props.UserInfo}
                        History={this.props.History}
                        SetLoaderState={this.props.SetLoaderState} 
                    />
        }
        else if(activeTabs===2){
            return <MyFollowing 
                        TabsInfo={this.props.TabsInfo}
                        UserInfo={this.props.UserInfo}
                        History={this.props.History}
                        SetLoaderState={this.props.SetLoaderState} 
                    />
        }
        else if(activeTabs===3){
            return <MyFollowers 
                        TabsInfo={this.props.TabsInfo}
                        UserInfo={this.props.UserInfo}
                        History={this.props.History}
                        SetLoaderState={this.props.SetLoaderState} 
                    />
        }
        else if(activeTabs===4){
            return <MyHistory 
                        TabsInfo={this.props.TabsInfo}
                        UserInfo={this.props.UserInfo}
                        History={this.props.History}
                        SetLoaderState={this.props.SetLoaderState} 
                    />
        }
        else return <>
            <div className="row text-light">
                <div className="col-10 p-5 m-5 d-flex justify-content-center">
                    <p className='border border-light p-5 m-5'>aucune donnee {this.props.ActiveTabs}</p>
                </div>
            </div>
            </>
    }
}