import React from 'react'
import {Link} from 'react-router-dom'
import './drop.css'
import IsConnected from '../../../helpers/isConnected'
import {getSession} from '../../../helpers/session'
import GrisFond from './fond'

export default class DashBoard extends React.Component {
    constructor(props){
        super(props)
    }

    isAdmin = () =>{
        return getSession('userInfo').admin
    }

    render(){
        if(this.isAdmin()) return <div >
            <div>
                <button className='btn bg-transparent border-light rounded-0 text-light mx-3' onClick={(e)=>{this.props.History.history.push('/dashboard/updateProduits')}} > Dashboard </button>
            </div>
            </div>
        else return <></>
    }
}
