import React from 'react'
import axios from 'axios'
import env from '../../../config/api'
import RegisterView  from './registerView'
import registerStore from './registerStore'
import flashed from '../../../helpers/flashed'


export default class register extends React.Component {
  constructor(props){
    super(props)
    this.state=registerStore
}

save_data = (key='temp',data={}) =>{
  localStorage.setItem(key,JSON.stringify(data))
}

isEmpty = (data) =>{
  let is_empty = false
  data.forEach(element => {
    if(element.length<1) is_empty=true
  })
  return is_empty
}

recup_data = (key='temp') => {
  return JSON.parse(localStorage.getItem(key))
}

loading = (start=true) => {
  let loading = this.state.loading
  const current =start
  loading.btn=current
  loading.spiner=current

  this.setState({loading})
}

form_control = (valide=true) =>{
  const is_valide=' '
  const is_invalide='is-invalid'
  this.setState({form_control:valide?is_valide:is_invalide})
}

alert_control = (show=true,alert_msg) =>{
  this.setState({alert:show})
  this.setState({alert_msg})
  // console.log(alert_msg);
}

error_ocurred = (msg) =>{
  this.setState({msg})
  this.form_control(false)
  this.alert_control(true,msg)
  setTimeout(() => {
    this.form_control(true)
    this.alert_control(false,msg)
  }, 2000);

}
success_register = (data={}) =>{
  flashed('set',{
    username:this.state.input.username,
    password:this.state.input.password,
    time:new Date().getTime()
  })
  this.props.history.push('login')
}

inputChange = (e) =>{
  const id=e.nativeEvent.target.id
  const defined_id=[
    ['username','username'],
    ['password','password'],
    ['email','email'],
    ['repassword','repassword']
  ]
  const true_id=defined_id.map((index)=>{
    if (id==index[1]){
      return index[0]
    }
  }).filter((data)=>{
    if(data!=undefined){
      return data
    }
  })
  let input = this.state.input
  input[true_id[0]] = e.target.value

  this.setState({input})
}
submited=(e)=>{
  // console.log(e);
}

passwordVerification = () =>{
  return (this.state.input.password===this.state.input.repassword)
}

register =(e) =>{
  e.preventDefault()
  const input = this.state.input
  if (!this.passwordVerification()) {
    this.error_ocurred('check password')
    // console.log('check password');
    return 0
  }
  else if (this.isEmpty([input.email,input.password,input.repassword,input.username])){
    const msg='input can not empty'
    this.error_ocurred(msg)
    // console.log(msg);
    return 0
  }
  else{
    this.loading(true)
    const url = env.hostname+env.routes.register+this.state.input.email+'/'+this.state.input.username+'/'+this.state.input.password
    axios.get(url)
      .then((res)=>{
        if(res.data.result==null){
          this.error_ocurred(res.data.error)
        }
        else if(res.data.result!=null){
          this.success_register(res.data.result)
        }
      }).catch((res)=>{
        this.error_ocurred('bad requests')
      }).finally((res)=>{
        this.loading(false)
        // console.log(res)
    })
  }
  
}
render(){
  return(
    <RegisterView
      viewController={this.state}
      inputChange={this.inputChange}
      submited={this.submited} 
      register={this.register} 
    />
      )
    }
}
