import React from 'react'
import {Link} from 'react-router-dom'
import './drop.css'
import IsConnected from '../../../helpers/isConnected'
import {getSession} from '../../../helpers/session'
import GrisFond from './fond'

export default class NavBar extends React.Component {
    constructor(props){
        super(props)
        console.log('i am monted');
        this.state={
            show_drop:false,
            is_connected:false,
            Fond:false,
            zIndex:100,
        }
    }
    componentDidMount(){
        this.setState({is_connected:IsConnected()}) 
        console.log(this.props.History)
    }

    linkTologin = () =>{
        this.props.History.history.push('/login')
    }

    not_connected = () =>{
        console.log('connect you');
    }

    zIndex = (zIndex) =>{
        this.setState({zIndex})
    }

    drop = (e) =>{
        const show_drop = !this.state.show_drop
        this.fondEnabled(show_drop)
        this.zIndex(!show_drop?100:350)
        if(!this.state.is_connected){
            this.not_connected()
            return 0
        }
        this.setState({show_drop})
        console.log(this.state.show_drop);
    }
    myProfilLink = () =>{
        const userInfo = getSession('userInfo')
        return userInfo.username
    }
    
    drop_show = () =>{
        const show_drop = this.state.show_drop
        let classer = 'bg-dark text-light text-center border border-light '
        if(show_drop) return classer
        else return classer+' d-none'
    }
    fondEnabled = (Fond) =>{
        this.setState({Fond})
        this.setState({show_drop:Fond})
    }

    render(){
        if(this.state.is_connected) return <div>
            <div style={{zIndex:this.state.zIndex}}>
                <button className='btn bg-transparent border-light rounded-circle text-light' onClick={(e)=>{this.drop(e)}} ><i className="fas fa-user"  ></i></button>
            </div>
            <div id='drop' className={this.drop_show()} style={{zIndex:this.state.zIndex}} >
                <Link className='Link' to={'/profil/'+this.myProfilLink()}>profil</Link>
                <Link className='Link' to=''>settings</Link>
                <Link className='Link' to=''>about</Link>
            </div>
                <GrisFond fond={this.state.Fond} fondEnabled={this.fondEnabled} Zindex={300} />
            </div>
        else return <div>
            <button className='btn btn-dark border rounded-0 border-light px-3' onClick={()=>{this.linkTologin()}}> sign-in </button>
        </div>
    }
}
