import React from 'react'
import { Link } from 'react-router-dom'
import './profil.css'
import Statistic from './statistic'
import UserInfo from './userInfo' 
import PostTabs from './tabs/myPost'
import { getSession } from '../../helpers/session'


export default class ProfilView extends React.Component {
    constructor(props){
        super(props)
        // console.log(this.props.History);
    } 

    isMyProfile = () =>{
        if(getSession('userInfo')==null) return false
        if(this.props.History.match.params.username==getSession('userInfo').username ) return true
        else return false
    }

    componentDidMount(){
        
    }
    componentDidUpdate(){
        //console.log(this.props.UserInfo)
    }
    render(){
        const userInfo = this.props.UserInfo
        if(this.props.UserInfo!==null && this.isMyProfile() ) return <div className='bg-dark container'>
            <UserInfo UserInfo={this.props.UserInfo} History={this.props.History} SetLoaderState={this.props.SetLoaderState} />
            <Statistic UserInfo={this.props.UserInfo} History={this.props.History} SetLoaderState={this.props.SetLoaderState}/>
        </div>
        else if (this.props.UserInfo!==null && !this.isMyProfile()){ return <>
            <UserInfo UserInfo={this.props.UserInfo} History={this.props.History} SetLoaderState={this.props.SetLoaderState} />
            <div className="row col-12">
                <h4 className='text-light mx-5'>recent post </h4>
            </div>
            <PostTabs UserInfo={this.props.UserInfo} />
        </>}
        else return<div className='d-flex justify-content-center border border-light m-5 p-5'>
        <h2 className='text-light p-3 m-3'> user <span className='text-secondary'>{this.props.History.match.params.username}</span> not found <Link to='/' className='text-secondary'> go to home</Link> </h2>
        </div>
        
    }
}