
const envbuilder =  (env) =>{

    let api = {
        hostname:null,
        env,
        routes:{

            ProduitsInfo:'produitsInfo/',
            AllProduitsInfo:'allProduitsInfo/',
            newProduits:'setNewProduit/',
            SellProduits:'updateProduitInfo/',
            Notification:'getNotification/',
            deleteProduit:'deleteProduit/',
            sudoUpdateProduitsInfo:'sudoUpdateProduitsInfo/',
            allUserInfo:'sudoGetAllUserInfo/',
            deleteUser:'sudoDeleteUser',
            //
            login:'login/',
            register:'register/',
            getUserProfil:'userInfo/',
            connection:'connection/',
        }
    }

    const hostname = {
        'dev':
        {
            hostname:'http://localhost:5000/api/',
            
        },
        'prod':{
            hostname:'https://tricky-sulky-edam.glitch.me/api/',
        },
    }

    api.hostname=hostname[env].hostname
    return api
}

export default envbuilder('prod') // env = 'prod' for production or 'dev' for developement