import React from 'react'
import './modal.css'
import GrisFond from '../navbar/fond'

export default class Modal extends React.Component {

    constructor(props){
        super(props)
        this.state={
            show_drop:false,
            Fond:false,
            zIndex:100,
        }
    }

    zIndex = (zIndex) =>{
        this.setState({zIndex})
    }

    drop = (e) =>{
        const show_drop = !this.state.show_drop
        this.fondEnabled(show_drop)
        this.zIndex(!show_drop?100:350)
        this.setState({show_drop})
    }
    
    drop_show = () =>{
        const show_drop = this.state.show_drop
        let classer = ' text-light text-center '
        if(show_drop) return classer
        else return classer+' d-none'
    }
    fondEnabled = (Fond) =>{
        this.setState({Fond})
        this.setState({show_drop:Fond})
        if(Fond==false){
            if(this.props.refuse) this.props.refuse()
        }
    }

    modalCardStyle = () =>{
        if (this.props.modalCardStyle) return this.props.modalCardStyle
        return {top:'40%',left:'35%'}
    }

    buttonGroupe =() =>{
        if(this.props.confirm) return <div>
                <button className='btn btn-dark border rounded-0 border-light px-3 m-2  col-4' onClick={()=>{this.props.confirm();this.fondEnabled(false)}}>{this.props.message.confirm}</button>
                <button className='btn btn-dark border rounded-0 border-light px-3 m-2  col-4' onClick={()=>{this.props.refuse();this.fondEnabled(false)}}> {this.props.message.refuse} </button>
            </div>
        else return <></>
    }

    render(){
        return <>
                <button className={this.props.ModalButton.class} onClick={(e)=>{this.drop(e)}}>{this.props.ModalButton.msg}</button>
            <div id='drop' className={this.drop_show()+' col-3'} style={{zIndex:this.state.zIndex}} >
                <div className={"modal-card bg-dark "+this.props.ModalButton.box_class} style={this.modalCardStyle()}>
                    <div className="row">
                        <>{this.props.message.msg}</>
                        <this.buttonGroupe />
                    </div>
                </div>
                
            </div>
                <GrisFond fond={this.state.Fond} fondEnabled={this.fondEnabled} Zindex={300} />
            </>
    }
}
