import React from 'react'
import './profil.css'
import Tabs from './tabs'

export default class Statistic extends React.Component {
    constructor(props){
        super(props)
        this.state={
            active_tab:0,
            statistic_area:{
                data:{}
            },
            tabs:[
                {
                    value:'mes publications',
                    badge:this.badge('post'),
                    disabled:true,
                    click:(index)=>{this.changeInfoArea(index)}
                },
                {
                    value:'mes sauvegardes',
                    badge:this.badge('bookmark'),
                    disabled:false,
                    click:(index)=>{this.changeInfoArea(index)}
                },
                {
                    value:'mes abonements',
                    badge:this.badge('follow'),
                    disabled:false,
                    click:(index)=>{this.changeInfoArea(index)}
                },
                {
                    value:'mes abonnees',
                    badge:this.badge('followers'),
                    disabled:false,
                    click:(index)=>{this.changeInfoArea(index)}
                },
                {
                    value:'historiques',
                    badge:this.badge('view'),
                    disabled:false,
                    click:(index)=>{this.changeInfoArea(index)}
                },
            ]
        }
    }

    badge = (name) =>{
        const data = this.props.UserInfo[name]
        if(data==null) return 0
        return JSON.parse(data).length
    }
    changeInfoArea = (index) =>{
        let tabs = this.state.tabs
        tabs=tabs.filter((tab,index2)=>{
            if(index==index2){
                tab.disabled=true
                this.setState({active_tab:index2})
                return tab
            }
            else {
                tab.disabled=false
                return tab
            }
        })
        this.setState({tabs})
    }

    render(){return <>
            <div className="row text-light d-flex justify-content-center">
                {this.state.tabs.map((data,index)=>{
                    return  <button disabled={data.disabled}  onClick={(e)=>{data.click(index)}} className={"border p-2 m-2 border-bottom btn bg-transparent align-self-center rounded-0 text-light col-2  border-light text-center"}>
                                {data.value} <span class="badge badge-light border text-center">{data.badge}</span>
                            </button>
                })}
            </div>
            <Tabs ActiveTabs={this.state.active_tab} TabsInfo={this.state.statistic_area} UserInfo={this.props.UserInfo} History={this.props.History} SetLoaderState={this.props.SetLoaderState}/>
            </>
    }
}