import React from 'react'
import './card.css'
import {textShurt,numShurt} from '../../../helpers/shurtner'
import DeleteUser from './features/deleteUser'
import UserInfo from './features/userInfo'
import UserSell from './features/userSell'
import UserStatistic from './features/userStatistic'

export default class UsersCard extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      user:null,
      isReady:false
    }
  }

    render(){
      const user = this.props.user
        return(
          <tr className='row'>
            <div class=" col-12 btn box rounded-0">
            <div class="card bg-transparent text-white border-0 rounded-0 my-1 " >
            <div class="card-body d-flex justify-content-around">
              <td className='col-1'><p className=''>{this.props.index}</p></td>
              <td className='col-2'><h5 class="card-title text-primary h5  " title={''}>{textShurt(user.username,10)}</h5></td>
              <td className='col-2'><p class="card-text " title={''}>{textShurt(user.email,10)} </p> </td>
              <td className='col-4'>
                <>
                  <UserStatistic History={this.props.History} getAllUsersInfo={this.props.getAllUsersInfo} User ={user} />
                  <UserInfo History={this.props.History} getAllUsersInfo={this.props.getAllUsersInfo} User ={user} />
                  <UserSell History={this.props.History} getAllUsersInfo={this.props.getAllUsersInfo} User ={user} />
                  <DeleteUser History={this.props.History} getAllUsersInfo={this.props.getAllUsersInfo} User ={user} />
                </>
              </td>
            </div>
        </div>
        </div>
        </tr>
        )
    }
}
