export default class FormStateManager {
    
    static inputChange = (e,idMap) => {
        const id = e.nativeEvent.target.id
        const true_id = idMap.map((index)=>{
          if (id==index){
            return index
            console.log(index);
          }
        }).filter((data)=>{
          if(data!=undefined){
            return data
          }
        })

        return [true_id,e.target.value]
        
      }

      static numberChange = (e,idMap) => {
        const id = e.nativeEvent.target.id
        const true_id = idMap.map((index)=>{
          if (id==index){
            return index
            console.log(index);
          }
        }).filter((data)=>{
          if(data!=undefined){
            return data
          }
        })
        let val  = parseInt(e.target.value)
        if(!val) val=0

        return [true_id,]
        
      }
      
}