import React from 'react'
import axios from 'axios'
import api from '../../../config/api'

export default class UserInfoRequests extends React.Component {
    constructor(props){
        super(props)
        this.PostLength = JSON.parse(this.props.UserInfo).length
        this.state = {
            postList : new Array(this.PostLength),
            postReady: new Array(this.PostLength),
            isReady:false
        }
        
    }

    componentDidMount(){
        this.loadingInfo()
        
    }
    componentDidUpdate(){
        // console.log('updated')
    }

    loadingInfo = () =>{
        this.getPostInfo()
    }

    getPostInfo = () =>{
        let postList= this.state.postList
        let postReady = this.state.postReady
        const userPost = JSON.parse(this.props.UserInfo)
        // console.log(userPost)
        for (let index = 0; index < userPost.length; index++) {
            const id = userPost[index]
            const url = api.hostname+api.routes.getMyRelation+id
            // console.log(url)
            axios.get(url)
            .then((res)=>{
                // console.log(res.data);
                postList[index]= res.data.result 
                postReady[index]=true
                this.setState({postList})
                this.setState({postReady})
                this.setState({isReady:true})
            })
            .catch((err)=>{

            })
            .finally(()=>{

            })
        }
        
    }

    render(){
        let Card =  this.props.Card
        if(this.state.isReady){
            return <div className="row text-light p-2 d-flex justify-content-center mt-2 "> 
                {this.state.postList.map((post,index) =>{
                // console.log(post,index)
                if(post==null) return <></>
                return <Card cardInfo={post} /> 
                })}
            </div>
        } 
        else return <div>
            <p>chargement </p>
        </div>
    }
}