import React from 'react'
import Modal from '../../../home/controlui/modal'

export default class UserInfo extends React.Component {
  constructor(props){
    super(props)
  }

  userInfoJsx = () => {
    const user = this.props.User
    return <>
    <p className='h5 p-2'> information de l'utilisateur</p><div className='text-light'>
      <p> numero du compte : {user.id} </p>
      <p>identifient : {user.username}</p>
      <p> email: {user.email} </p>
      <p> administrateur : {user.admin?'oui':'non'} </p>
    </div></>
  }

    render(){
      const user = this.props.User
        return <Modal History={this.props.History} 
            message = {{ msg:<this.userInfoJsx />}}
            ModalButton={{class:'btn align-self-center text-light border-0 rounded-0 btn-icon mb-3',msg:<i className='fas fa-user '></i>,box_class:'col-3'}}
        />
    }
}
