import React from 'react'

export default class Wacther extends React.Component {
    constructor(props){
        super(props)
        this.state={
            history:{
                audio:[]
            }
        }
    }
    appendHistory = (name='',value='') =>{
        let history =this.state.history
        history[name].push(value)
        this.setState({history})
      }
  
      checkHistory = (name='',value='') =>{
        let history =this.state.history
        let main =history[name]
        if(main[main.length-1]!==value) return 1
        return 0
      }
    
    updateHistory =() =>{
        const title =this.props.History.match.params.title
        const isupdated = this.checkHistory('audio',title)
        if(isupdated===0) return 0
        console.log('is updated');
       //  this.SetLoaderState(true)
        this.appendHistory('audio',title)
        this.props.Tracked()
    }

    componentDidUpdate(){
        this.updateHistory()
    }
    
    render(){
        return<></>
    }
}