import React from 'react'
import Modal from '../../../home/controlui/modal'
import FST from '../../../../helpers/formStateManager'

export default class UserStatistic extends React.Component {
  constructor(props){
    super(props)
    this.state={
        userInfo:null,
        userSaved:null,
        periode:{
            heures:null,
            jours:null,
            mois:null,
            annees:null,
            active:null
        },
        input:{
           num:0,
           max:null
        }
    }
    this.inputMax = {
        heures:24,
        jours:31,
        mois:12,
        annees:50,
    }
  }

  componentDidMount(){
      this.userSellingJsonner()
      this.changePeriod('heures')
      console.log(this.props.User)
  }

  inputAcceptable = (value) => {
    if(value>this.state.input.max){
      let input  = this.state.input
      input.num=this.state.input.max
      this.setState({input})
      return false
    } 
    else if (value<0) return false
    return true
  } 

  inputIsValide = (e) =>{
    const enter = e.target.value
    console.log(parseInt(enter))
    return parseInt(enter)
  }

  inputChange = (e) =>{
    const val = parseInt(this.inputIsValide(e))
    if(val==='NaN'){
      return 0
    }
    else if (isNaN(val)) return 0
    const idMap = ['num']
    const res = FST.inputChange(e,idMap)
    let input =  this.state.input
    if(res[1].length<1) res[1]='0'
    input.num = parseInt(res[1])
    if(!this.inputAcceptable(input.num)) return 0
    this.setState({input})
    this.trieEngine()
  }


  titleJsx = () =>{
    if(this.state.userInfo.sellRegistre.length>=1)
      return <div className='row px-2'>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">nombre de produit </p>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">unite vendu </p>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">prix de vente (FCFA)</p>
          <p className="col-3 btn bg-transparent text-light rounded-0 ">marge de benefice  (FCFA)</p>
      </div>
    else return <div className='row px-2'>
      <p className='h5 m-3 text-center'> aucune vente enregister par cet utilisateur </p>
    </div>
  }

  dateBeautify =(date) =>{
    return ` ${date.jours}/${date.mois}/${date.annees}  ${date.heures}:${date.minutes}:${date.secondes}`
  }

  changePeriod =(name) =>{
    let periode={
        heures:null,
        jours:null,
        mois:null,
        annees:null,
        active:null
    }
    periode[name]= true
    periode.active = name

    this.setState({periode,input:{num:0,max:this.inputMax[name]}})
    this.trieEngine()
  }

  periodeIcon = (name) =>{
      const periode = this.state.periode
      return periode[name]?'fas fa-dot-circle':'fas fa-circle'
  }

  totalCount = () =>{
      const userInfo = this.state.userSaved
      return userInfo
  }

  userInfoSaved = (method) =>{
    let userInfo = this.props.User
    if (method==='set'){
       userInfo.sellRegistre = JSON.parse(userInfo.sellRegistre)
        this.setState({userSaved:userInfo})
    }
    else if (method==='get'){
        return this.state.userSaved
    }
  }

  trieEngine =() =>{
    let userInfo = this.userInfoSaved('get')
    if(!userInfo) return 0
    if(this.state.input.num<=0) {
        this.setState({userInfo,input:{num:0}})
        return 0
    }
    console.log(userInfo.sellRegistre);
    const DateInt = new Date()
    const curDate = {
        temps:DateInt.getTime(),
        annees:DateInt.getFullYear(),
        mois:DateInt.getMonth(),
        jours:DateInt.getDate(),
        heures:DateInt.getHours(),
        minutes:DateInt.getMinutes(),
        secondes:DateInt.getSeconds(),
        millisecondes:DateInt.getMilliseconds(),
    }
    userInfo.sellRegistre = userInfo.sellRegistre.filter((info)=>{
        const periode = info.date[this.state.periode.active]
        const num = this.state.input.num
        if(periode>curDate[this.state.periode.active]-num) return info
    })
    this.setState({userInfo})
  }

  trieJsx = () =>{
      return <div className='row py-3 border rounded-0'>
           <p className='col-4'>ventes des {this.state.input.num} derniers {this.state.periode.active}</p>
           <div className="col-2">
           <input type="text" name="" id="" className='form-control text-center bg-transparent text-light border p-1 rounded-0 ' value={this.state.input.num} onChange={(e)=>{this.inputChange(e)}}/>
           </div>
           <div className='col-6 row' >
               <p className='col-3 btn bg-transparent text-light' name='heures' onClick={()=>{this.changePeriod('heures')}}> heures <i className={this.periodeIcon('heures')}></i> </p>
               <p className='col-3 btn bg-transparent text-light' name='jours'  onClick={()=>{this.changePeriod('jours')}}> jours <i className={this.periodeIcon('jours')}></i> </p>
               <p className='col-3 btn bg-transparent text-light' name='mois'   onClick={()=>{this.changePeriod('mois')}}> mois <i className={this.periodeIcon('mois')}></i> </p>
               <p className='col-3 btn bg-transparent text-light' name='annees' onClick={()=>{this.changePeriod('annees')}} > annees <i className={this.periodeIcon('annees')}></i> </p>
           </div>
      </div>
  } 

  userInfoJsx = () => {
    const userInfo = this.state.userInfo 
    if(userInfo!=null) return <div className='text-light'>
        <p className='h3 p-2'> statistique en terme de vente de <em>{userInfo.username}</em>  </p>
        <this.trieJsx />
        <this.titleJsx />
      {userInfo.sellRegistre.map((registre)=>{
          return <div className='row text-center   m-2 px-4'>
              <p className="col-3 border-0 rounded-0 "> {this.dateBeautify(registre.date)}</p>
              <p className="col-3 border-0 rounded-0 ">{registre.produitInfo.stock}</p>
              <p className="col-3 border-0 rounded-0 ">{registre.produitInfo.produitTable.prix_de_vente*registre.produitInfo.stock} </p>
              <p className="col-3 border-0 rounded-0 "> {(registre.produitInfo.produitTable.prix_de_vente-registre.produitInfo.produitTable.prix_d_achats)*registre.produitInfo.stock} </p>
          </div>
      })}
    </div>
    else return <></>
  }

  userSellingJsonner = () => {
    let userInfo = this.props.User
    userInfo.sellRegistre = JSON.parse(userInfo.sellRegistre)
    this.setState({userInfo})
    this.userInfoSaved('set')
    //console.log(userInfo)
  }

    render(){
        return <Modal History={this.props.History} 
            message = {{ msg:<this.userInfoJsx />}}
            modalCardStyle={{top:'17%',left:'5%'}}
            ModalButton={{class:'btn align-self-center text-light border-0 rounded-0 btn-icon mb-3',msg:<i className='fas fa-eye '></i>,box_class:'col-11'}}
        />
    }
}
