import React from 'react'

export default class ProduitTotal extends React.Component {
    constructor(props){
        super(props)
    }

    render(){
        const count = this.props.count
        return <div className="row  rounded-0 py-3 d-flex px-2 ">
        <p className='col-4 align-self-center h6'>Total</p> 
        <div className="col-3 align-self-center">
          <input class="form-control  bg-dark text-white rounded-0 text-center" type="text" placeholder="nombre"
          aria-label="Search" id='num' autoComplete='false' value={count.quantite} disabled='true' />
        </div>
        <div className="col-5  h6">
          <p className='align-self-center'>{count.price} FCFA</p>
        </div>
      </div>
    }
}
