import React  from 'react'
import {BrowserRouter,Route,HashRouter} from 'react-router-dom'
import Details from '../components/details/index'
import Home from '../components/home/mainPage/index'
import Profil from '../components/profile/index'
import Login from '../components/auth/login/login'
import Register from '../components/auth/register/register'
import Css from '../components/section/css'
import Seo from '../components/section/seo'
import Dashboard from '../components/dashboard/index'
import Redirect from '../helpers/redirect'
import isConnected from '../helpers/isConnected'
import ConnectYou from '../components/auth/connectionRequired/required'
import RouteWatch from './routeWatch'


export default class Routes extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            isConnected:false
        }
    }

    componentDidMount(){
        this.updateIsConnected()
    }

    updateIsConnected = () =>{
        this.setState({isConnected:isConnected()})
    }

    routesTracker = () =>{
        this.updateIsConnected()
        console.log('route tracked is called')
    }

   
    render(){
        if(this.state.isConnected) return <>
           <Seo/>
           <Css/>
           <HashRouter>
                <Route exact path="/" component={Home}/>
                <Route exact path="/produit/:id" component={Details}/>
                <Route exact  path="/profil/:username" component={Profil}/>
                <Route exact  path="/dashboard/:app" component={Dashboard}/>
                <Route exact  path="/redirect/:route" component={Redirect}/>

                <Route exact path="/login" component={Login} />
                <Route exact path="/register" component={Register} />
                
           </HashRouter>
        </>

        else return <>
            <Seo/>
            <Css/>
            <HashRouter> 
                <Route exact path="/login" component={Login} />
                <Route exact path="/register" component={Register} />
                <Route exact path="/" component={ConnectYou}/> 
            </HashRouter>
        </>
    } 
}