import React from 'react'
import {Link} from 'react-router-dom'
import { numShurt, textShurt } from '../../../helpers/shurtner'

export default class MyPostCard extends React.Component {
    constructor(props){
        super(props)
    }

    render(){ 
        const post = this.props.cardInfo
        // console.log('render 122')
        return( 
        <div class="card bg-transparent col-3 mx-2 my-2 d-flex flex-column justify-content-between border-light text-light"
        style={{
            minHeight:200,
            maxHeight:200,
        }}
        >
            <div class="card-body d-flex flex-column text-center justify-content-between">
                <Link to={'/audio/'+post.src} className='text-primary text-decoration-none '> <h5 class="card-title " title={post.title}>{textShurt(post.title,10)}</h5></Link>
                <h6 class="card-subtitle mb-2 text-muted" title={post.artiste}>{textShurt(post.artiste,10)}</h6>
                <p class="card-text" title={post.description}>{textShurt(post.description,20)}</p>
                <div className="d-flex justify-content-around">
                <em class=" text-center"><i className="fa fa-heart text-secondary"></i><strong className="m-2 text-secondary">{numShurt(post.like)}</strong></em>
                <em class=" text-center"><i className="fa fa-eye  text-secondary"></i><strong className="m-2 text-secondary">{numShurt(post.view)}</strong></em>
                <a href='#' class="text-center"><i className="fa fa-bookmark  text-secondary"></i></a>
                </div>
            </div>
        </div>)
    }
}