export default (method,data={}) =>{
    
    const key = 'flashed'
    let rtn=null
    if(method==='set'){
      sessionStorage.setItem(key,JSON.stringify(data)) 
      rtn = true
    }
    else if (method==='get'){
      rtn = JSON.parse(sessionStorage.getItem(key))
      sessionStorage.removeItem(key)
    }
  
    return rtn
    
  }