import React from 'react'
import {Link} from 'react-router-dom'
import './drop.css'
import IsConnected from '../../../helpers/isConnected'
import {getSession} from '../../../helpers/session'
import GrisFond from './fond'
import axios from 'axios'
import api from '../../../config/api'

export default class Notification extends React.Component {
    constructor(props){
        super(props)
        this.state={
            show_drop:false,
            is_connected:false,
            Fond:false,
            zIndex:100,
            Notification:[]
        }
    }
    componentDidMount(){
        this.setState({is_connected:IsConnected()}) 
        this.getNotification()
        console.log(this.props.History)
    }

    linkTologin = () =>{
        this.props.History.history.push('/login')
    }

    not_connected = () =>{
        console.log('connect you');
    }

    zIndex = (zIndex) =>{
        this.setState({zIndex})
    }

    drop = (e) =>{
        const show_drop = !this.state.show_drop
        this.fondEnabled(show_drop)
        this.zIndex(!show_drop?100:350)
        if(!this.state.is_connected){
            this.not_connected()
            return 0
        }
        this.setState({show_drop})
        console.log(this.state.show_drop);
    }
    myProfilLink = () =>{
        const userInfo = getSession('userInfo')
        return userInfo.username
    }
    
    drop_show = () =>{
        const show_drop = this.state.show_drop
        let classer = 'bg-dark text-light text-center border border-light '
        if(show_drop) return classer
        else return classer+' d-none'
    }
    fondEnabled = (Fond) =>{
        this.setState({Fond})
        this.setState({show_drop:Fond})
    }
    getNotification =() =>{
        axios.get(api.hostname+api.routes.Notification).then((res)=>{
            const result  = res.data.result
            if(result!=null){
                this.setState({Notification:result})
            }
        }).catch((err)=>{

        }).finally(()=>{

        })
    }

    render(){
        if(this.state.Notification.length>1) return <div className='px-4'>
            <div style={{zIndex:this.state.zIndex}}>
                <button className='btn bg-transparent border-light rounded-0 text-light' onClick={(e)=>{this.drop(e)}} ><i className="fas fa-bell"  ></i><span class="badge badge-light ">{this.state.Notification.length}</span></button>
            </div>
            <div id='drop' className={this.drop_show()+' col-3 d-flex flex-column'} style={{zIndex:this.state.zIndex,right:'7%'}} >
                {this.state.Notification.map((produit,index)=>{
                   return <p className={'btn border bg-transparent rounded-0 text-light p-2 col-12  border-light'} onClick={(e)=>{this.props.History.history.push('/produit/'+produit.id);this.drop('')}}>{produit.notification}</p> 
                })}
            </div>
                <GrisFond fond={this.state.Fond} fondEnabled={this.fondEnabled} Zindex={300} />
            </div>
        else return <div className='px-4'>
            <div style={{zIndex:this.state.zIndex}}>
                <button className='btn bg-transparent border-light rounded-0 text-light' onClick={(e)=>{this.drop(e)}}><i className="fas fa-bell"  ></i></button>
            </div>
            <div id='drop' className={this.drop_show()+' col-3 d-flex flex-column'} style={{zIndex:this.state.zIndex,right:'7%'}} >
                <p className='bg-transparent rounded-0 text-light p-2 col-12 '>aucune notification pour l'instant dans la boite</p> 
            </div>
            <GrisFond fond={this.state.Fond} fondEnabled={this.fondEnabled} Zindex={300} />
        </div>
    }
}
