export  const textShurt = (text,max) => {
    let shurted = ''
    let isTooLong = true
    if(max>text.length){
        max = text.length
        isTooLong = false
    }
    for (let index = 0; index < max; index++) {
        const element = text[index];
        shurted+=element
    }
    return isTooLong?shurted+'...':shurted
}

export  const numShurt = (num) => {
    if(num<1000){
      return num
    }
    else if (num >=1000){
      return parseInt(num/1000)+'K'
    }
    else if (num >=1000**2){
        return parseInt(num/(1000**2))+'M'
    }
}