import React from 'react'
import NavBar from '../navbar/navbar'
import ProduitsController from './produitsController'
import Footer from '../../section/footer'
import Loader from '../controlui/loader'
import theme from '../../../data/state/theme'

export default class Home extends React.Component {
    constructor(props){
        super(props)
        this.state={
            LoaderState:true,
            theme,
            themeActivate:'dark'
        }
        this.theme=this.state.theme[this.state.themeActivate]
        //this.theme = this.state.theme.dark
    }
    componentDidMount(){
        this.randomTheme()
    }
    SetLoaderState =(NewState) =>{
        this.setState({LoaderState:NewState})
    }
    randomTheme=()=>{
        setTimeout(() => {
            this.setState({themeActivate:this.state.themeActivate=='dark'?'light':'dark'})
            console.log(this.state.themeActivate);
        }, 5000);
        
    }
    render(){
        return(
<div className={"container-scroller "+this.theme.primary} >
    <Loader LoaderState={this.state.LoaderState} Theme={this.theme} />
      <NavBar History={this.props} Theme={this.theme}/>
      <div class="container-fluid page-body-wrapper container">
        <ProduitsController History={this.props} SetLoaderState={this.SetLoaderState} Theme={this.theme} />
        <Footer Theme={this.theme} />
        </div>  
    </div>
        )
    }
}