import React from 'react'
import NavBar from '../home/navbar/navbar'
import ProfilView from './profileView'
import Footer from '../section/footer'
import Loader from '../home/controlui/loader'
import theme from '../../data/state/theme'
import axios from 'axios'
import env from '../../config/api'
import Watcher from '../home/controlui/watcher'

export default class Profile extends React.Component {
    constructor(props){
        console.log(props);
        super(props)
        this.state={
            LoaderState:true,
            theme,
            themeActivate:'dark',
            userinfo:null
        }
        this.theme=this.state.theme[this.state.themeActivate]
    }
    componentDidMount(){
        this.getUserInfo()
    }
    getUrlParams = () => {
        return this.props.match.params
    }

    getUserInfo = () =>{
        axios.get(env.hostname+env.routes.getUserProfil+this.getUrlParams().username).then((res)=>{
            this.setState({userinfo:res.data.result})
            console.log(res.data);
        }).catch((err)=>{

        }).finally(()=>{
            this.SetLoaderState(false)
        })
    }

    SetLoaderState =(NewState) =>{
        this.setState({LoaderState:NewState})
    }

    routesTracker = () =>{
        this.SetLoaderState(true)
        this.getUserInfo()
    }

    render(){
        return(
    <div className={"container-scroller "+this.theme.primary} >
        <Watcher History={this.props.match.params.username} Tracked={this.routesTracker} WatcherId={120}/>
        <Loader LoaderState={this.state.LoaderState} Theme={this.theme} />
        <NavBar Theme={this.theme} History={this.props}/>
        <div class="container-fluid page-body-wrapper container">
            <ProfilView UserInfo={this.state.userinfo} History={this.props} SetLoaderState={this.SetLoaderState} Theme={this.theme} />
            <Footer Theme={this.theme} />
        </div>
    </div>
        )
    }
}