import axios from 'axios'
import React from 'react'
import api from '../../../../config/api'
import { getSession } from '../../../../helpers/session'
import Modal from '../../../home/controlui/modal'

export default class DeleteUser extends React.Component {
  constructor(props){
    super(props)
  }

  deleteUser = () => {
      const data = {
          acountInfo:this.props.User,
          userInfo:getSession('userInfo'),
          Token:getSession('Token')
      }
      axios.post(api.hostname+api.routes.deleteUser,data).then((res)=>{
        const result = res.data.result
        if(result!=null){
          this.props.getAllUsersInfo()
        }
      }).catch((err)=>{

      }).finally(()=>{

      })
  }

    render(){
      const user = this.props.User
        return <Modal History={this.props.History} 
            message = {{ msg:<p className=''> vous allez suprimer <strong>{user.username}</strong> de la liste des utilisateurs  <br />attention cette opreation est irreversible <br /> <strong className="h6"> voulez vous continuez ? </strong>  </p>,confirm:'oui',refuse:'non'}}
            confirm={(e)=>{this.deleteUser()}}
            refuse={(e)=>{}}
            ModalButton={{class:'btn align-self-center text-light border-0 rounded-0 btn-icon mb-3',msg:<i className='fas fa-trash '></i>,box_class:'col-3'}}
        />
    }
}
