import React from  'react'

export default class produitdCardTitle extends React.Component {
    constructor(props){
      super(props)
  }

  trieView = (attr) =>{
    const data = this.props.trie[attr]
    if(data==null) return ''
    else if (data=='up') return 'fas fa-arrow-up  pt-2'
    else if (data=='down') return 'fas fa-arrow-down  pt-2'
    else return ''
  }

  trieFunction = (data) => {
      this.props.trieFunction(data)
  }
  
      render(){
          return(
              <div class=" col-10">
              <div class="card bg-transparent text-white border-0 rounded-0 " >
              <div class="card-body h6   row ">
                  <p className='btn  text-light box align-self-center col-2' title='cliquer pous trie le tableau selon la periode ' onClick={(e)=>{this.trieFunction('index')}}>index <i className={this.trieView('index')}></i></p>
                  <p class="btn box col-2 text-light align-self-center " >nom du produit <i className={this.trieView('nom')}></i></p> 
                  <p class="btn box text-light col-4 align-self-center text-right " >description du produit <i className={this.trieView('description')}></i> </p> 
                  <p className='btn box text-light align-self-center col-2 ' >stock restant <i className={this.trieView('stock')}></i></p>
                  <p className='text-light align-self-center col-2 '>details du produits</p>
              </div>
          </div>
          </div>
          )
      }
  }