import React from 'react'
import api from '../../../config/api'
import axios from 'axios'
import UsersCard from './card'
import Watcher from '../../home/controlui/watcher'
import Loading from '../../home/mainPage/loading'
import Search from '../../home/mainPage/search'
import CardTitle from './CardTitle'

export default class userManageControllers extends React.Component {
    constructor(props){
      super(props)
      this.state={
        Users:[],
        UserSaved:[],
        Search:'',
        trie:{
          index:'up',
          name:null,
          description:null,
          stock:null,
          active:'index'
        }
      }
    }
    componentDidMount(){
      this.getAllUsersInfo()
    }

    Tracked = () => {
      this.getAllUsersInfo()
    }

    getSourceName = () => {
      const title =this.props.History.match.params.title
      return title
    }

    updateSearch = (Search) =>{
      this.setState({Search})
      this.updateUsersView(Search)
      console.log(Search,this.state.Users,this.state.Search)
    }

    updateUsersView = (search) =>{
      let Users = this.state.UserSaved
      const text = ''
      Users = Users.filter((produit)=>{
        // console.log(produit.nom,search,produit.nom.search(search))
        if(produit.username.search(search)!=-1 ){
          return produit
        }
      })
      this.setState({Users})
    }
    
    getAllUsersInfo = () => {
      const UsersLenght=10
      this.props.SetLoaderState(true)
      axios.get(api.hostname+api.routes.allUserInfo ).then((res)=>{
        const Users=res.data.result
        if(res.data.result!=null){
          console.log(Users);
          this.setState({Users,UserSaved:Users})
        }
        
        this.props.SetLoaderState(false)
      }).catch(()=>{

      })
    }

    trieFunction = (data) =>{
      let method
      const trie = this.state.trie[data]
      this.setState({trie:{
        index:null,
        name:null,
        description:null,
        stock:null,
      }})
      method = trie=='up'?'down':'up'
      
      const state_trie=this.state.trie
      state_trie[data]=method
      state_trie.active = data
      this.setState({trie:state_trie})
        
      this.trieEngine(data,method)
      
    } 

    trieEngine = (data,method) =>{
      let Users = this.state.Users
      let trieTab = Users.map((produit)=>{
        return produit[data]
      })
      let trieTab2  = trieTab.sort()
      trieTab2 = trieTab2.reverse()
      
      let final_Users = Users.filter((produit)=>{
        let ret = undefined
        trieTab2.forEach((element,index) => {
          if(element == produit[data]){
            ret = element
            trieTab[index]=produit
          } 
        });
        if(ret!=undefined) return produit
      })
      
      this.setState({Users:trieTab})
      console.log(trieTab)
    }

    render(){
      const Users = this.state.Users
      if(this.state.UserSaved.length<=1) return <Loading />
      return(
      <div>
        <Watcher History={this.props.History} Tracked={this.Tracked} />
        <div class="">
            <div className="row d-flex justify-content-center ">
              <p className='text-white h5 m-4  underline'> la liste des Users </p>
              <hr/>
              <div className="row col-10">
                <div className="col-4">
                  <Search  History={this.props.History} 
                  updateSearch={this.updateSearch} 
                  Search={this.state.Search} />
                </div>
              </div>
              
              <CardTitle trie={this.state.trie} trieFunction = {this.trieFunction} />
              <table>
              {Users.map((user,index)=>{
                user.index=index
                return <UsersCard
                user={user} 
                index={index+1}
                Theme={this.props.theme} 
                History={this.props.History} 
                getAllUsersInfo={this.getAllUsersInfo}
                />})}
                </table> 
                
            </div>
        </div>
      </div>
      )
    }
}
