import React from 'react'
import axios from 'axios'
import api from '../../../config/api'
import ProduitCard from './card'
import Watcher from '../../home/controlui/watcher'
import { getSession } from '../../../helpers/session'

export default class produits extends React.Component {
    constructor(props){
        super(props)
        console.log(props);
        this.state={
          produit:{
            categories: null,
            created: null,
            description: undefined,
            id: undefined,
            image: "pc gameurs",
            nom: "pc",
            prix_d_achats: 80000,
            prix_de_vente: 130000,
            stock: 20,
            updated: 12982929,
          },
          watch:0,
        }
        this.SetLoaderState=this.props.SetLoaderState  
    }

    componentDidUpdate(){
      this.watchPanier()
    }

    packComponentDidMount = () =>{
      this.getProduitInformation()
    }

    packcomponentWillUnmount = () =>{
      console.log('unmounted');
    }

    componentDidMount(){
      this.packComponentDidMount()
    }
    
    componentWillUnmount(){
      this.packcomponentWillUnmount()
    }

    getSourceName(){
      this.url=this.props.History.location.pathname
      const audioSourceName=this.url.split('/')[this.url.split('/').length-1]
      return audioSourceName
    }

    getProduitInformation(){
      axios.get(`${api.hostname}${api.routes.ProduitsInfo}${this.getSourceName()}`).then((res)=>{
        const data=res.data
        console.log(data)
        if(data.result!=null){
          this.setState({produit:data.result})
        }
        
        
      }).catch((err)=>{

      }).finally(()=>{
        setTimeout(() => {
          this.SetLoaderState(false)
        }, 10);
      })
    }

    routesTracker = () => {
      console.log('routes updated');
      this.SetLoaderState(true)
      this.getProduitInformation()
    }

    sell = (stock) =>{
      this.SetLoaderState(true)
      const id = this.state.produit.id
      const produitTable = this.state.produit
      let data = {}
      data.produitInfo={id,stock,produitTable}
      data.userInfo = getSession('userInfo')
      data.Token = getSession('Token')
      axios.post(api.hostname+api.routes.SellProduits,data).then((res)=>{
        if(res.data.result!=null){
          this.getProduitInformation()
          console.log('selled')
        }
      }).catch((err)=>{

      }).finally(()=>{
        this.SetLoaderState(false)
      })
    }

    watchPanier = () =>{
      console.log(this.state.watch,this.props.panierIsSell)
      if(this.state.watch!=this.props.panierIsSell) {
        this.setState({watch:this.props.panierIsSell})
        this.getProduitInformation()
      }
    }

    render(){
      if(this.state.produit.id!=null){ return(
      <>
      <Watcher History={this.props.History.match.params.id} Tracked={this.routesTracker} WatcherId={120}/>
        <title>Miniki &gt; {this.getSourceName()}</title>
        <ProduitCard
          Informations={this.state.produit}
          CssClass={this.state.cssClass}
          Theme={this.props.Theme}
          History={this.props.History}
          panierIsUpdated ={this.props.panierIsUpdated}
          sell = {this.sell}
        />
      </>)}
  else {return(
    <>
    <p>chargement ...</p>
    </>
  )}
    }
}
